import React from 'react'
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';
import ProgressBar from 'react-bootstrap/ProgressBar';
import BottomBackNextBtn from 'views/mobileView/components/atoms/BottomBackNextBtn';
import db from 'libs/db';
const TaxStatus = (props) => {
	const [taxStatus, setTaxStatus] = React.useState('0');
	const [previousSelection,setPreviousSelection] = React.useState()
	const data = [
		{
			key: '0',
			data: 'Indian Resident - adult'
		},
		{
			key: '1',
			data: 'Indian Resident - minor'
		},
		{
			key: '2',
			data: 'NRI - adult'
		}
	];

	React.useEffect(() => {
		db.get(db.model.userData).then((u) => {
			if (u) {
				setTaxStatus(u.taxStatus || '0')
				if(u.taxStatus){
					setPreviousSelection(u.taxStatus || '0')
				}
			}
		})
	}, []);
	
	return (
		<div className='mainContainer'>
			<MainHeader
				header="Activate mutual funds"
				navigation={props.history}
			/>
			<ProgressBar now={10} className="progress-bar-custom" />
			<div className="dataContainer" style={{ paddingTop: '42px' }}>
				<div className="header1dark">Select investor tax status</div>

				{data.map((obj) => (
					<div
						key={obj.key}
						style={{
							display: 'flex',
							marginTop: "6%"
						}}>
						<div className="radio-item">
							<input
								type="radio"
								className='normalInput'
								id={obj.key}
								name="ritem"
								value={obj.key}
								onChange={(val) => {
									setTaxStatus(val.target.value)
								}}
								checked={taxStatus === obj.key}
							/>
							<label className="bodyTextDark radio-label" htmlFor={obj.key}>{obj.data}</label>
						</div>
					</div>
				))}
			</div>
			<div className='footer'>
				<BottomBackNextBtn
					navigation={props.history}
					buttonText="Confirm"
					clearData={previousSelection!==taxStatus}
					next={taxStatus === '1' ? "/activate-direct-mf/name" : "/activate-direct-mf/pan"}
					params={{ taxStatus }}
					saveData={{
						taxStatus
					}} />
			</div>
		</div>
	)
}

export default TaxStatus
